<style scoped>

</style>
<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" @click="showAddDialog" size="mini" icon="el-icon-plus">添加</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <region-city-store 
            label="选择区域" 
            className="search-input" 
            :showCity="false" 
            :showGroup="false" 
            :showStore="false" 
            @StoreGroupChange="StoreGroupChange"></region-city-store>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="table-wraper">
      <el-table
        class="fixed-height"
        ref="multipleTable"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column label="区域" min-width="120px">
          <template slot-scope="scope">
            <div>
              {{scope.row.regionCode}}-{{scope.row.regionName}}
            </div>
          </template>  
        </el-table-column>
        <el-table-column prop="discount" label="折扣" min-width="120px"></el-table-column>
        <el-table-column prop="createTime" label="创建时间" min-width="170px"></el-table-column>
        <el-table-column label="创建人" min-width="120px">
          <template slot-scope="scope">
            <div>
              {{scope.row.createUserId}}-{{scope.row.createUserName}}
            </div>
          </template>  
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="140">
          <template slot-scope="scope" size="small">
            <div> 
              <el-button @click="editItem(scope.row)" type="text" size="mini" icon="el-icon-edit">编辑</el-button>
              <el-button @click="deleteItem(scope.row)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      title="添加"
      :visible.sync="visible"
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      width="440px">
      <el-form 
        label-width="100px" 
        :rules="rules" 
        ref="ruleForm" 
        :model="addParamMsg">
        <region 
          v-if="visible"
          label="选择区域" 
          className="search-input" 
          :refresh="false" 
          required
          disabled></region>
        <el-form-item label="折扣" prop="discountReg">
          <el-input 
            class="search-input"
            v-model.trim="addParamMsg.discount" 
            placeholder="请输入折扣" 
            size="small" 
            clearable></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="visible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="doSetDisHandel('ruleForm')" size="mini" :disabled="idDisabled" :loading="idDisabled">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import RegionCityStore from '@/components/Common/StoreCommon/RegionCityStore'
  import Region from '@/components/Common/StoreCommon/Region'
  import Storage from '@/utils/localStorage'
  import config from '@/utils/config'
  import constant from '@/utils/constant'
  import requestUrl from '@/api/StoreMgt'
  export default {
    data() {
      const checkDiscount = (rule, value, callback) => {
        if (!this.addParamMsg.discount) {
          return callback(new Error('请输入折扣'));
        } else if (!constant.salePriceReg.test(this.addParamMsg.discount)) {
          callback(new Error('折扣限制只可输入数字，最多可输入2位小数'));
        } else {
          callback();
        }
      };
      return {
        idDisabled: false,
        loading: false,
        pageNum: 1,
        pageSize: 20,
        pageSizes: config.pageSizes20,
        total: 0,
        searchContent: {
          DIFFCITY: true,
          regionCode: Storage.get('regionCode') || '', // 区域ID
        },
        tableData: [],
        visible: false,
        addParamMsg:{
          id: '',
          regionCode: Storage.get('regionCode') || '',
          discount: ''
        },
        discount: '',
        rules: {
          discountReg: [
            { required: true, validator: checkDiscount, trigger: 'blur' }
          ]
        }
      }
    },
    components: {
      Region,
      'region-city-store': RegionCityStore
    },
    mounted() {
      this.doSearchHandel(true)
    },
    methods: {
      StoreGroupChange(result) {
        this.searchContent.regionCode = result.regionCode
        this.doSearchHandel(true)
      },
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      showAddDialog() {
        this.visible = true
        this.addParamMsg.regionCode = Storage.get('regionCode') || ''
        this.addParamMsg.discount = ''
        this.addParamMsg.id = ''
      },
      doSearchHandel(param) {
        this.loading = true
        if(param === true) {
          this.pageNum = 1
        }
        this.$http.post(requestUrl.selectRegionDiscountList, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            this.tableData = res.data && res.data.result && res.data.result.length ? res.data.result : []
            this.total = res.data.total
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      editItem(row) {
        this.visible = true
        this.addParamMsg.discount = row.discount
        this.addParamMsg.id = row.id
        this.addParamMsg.regionCode = row.regionCode || ''
      },
      deleteItem(row) {
        this.$confirm(`您确定要执行删除操作吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(requestUrl.deleteRegionDiscount, {id: row.id, DIFFCITY: true}).then(res => {
            if(res.code === 0) {
              this.$message.success(`删除成功`)
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      doSetDisHandel(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.idDisabled = true
            let path = this.addParamMsg.id ? requestUrl.editRegionDiscount : requestUrl.addRegionDiscount
            this.$http.post(path, {DIFFCITY: this.searchContent.DIFFCITY, ...this.addParamMsg}).then(res => {
              this.idDisabled = false
              if(res.code === 0) {
                this.$message.success("保存成功")
                this.visible = false
                this.doSearchHandel(true)
                return
              }
              throw new Error(res.msg)
            }).catch(e => {
              this.idDisabled = false
              this.$notify.error({
                title: '提示',
                message: e.message
              })
            })
          } else {
            return false;
          }
        });
      }
    }
  }
</script>


